<template>
  <div id="top" class="articleBox">
    <!-- 文章头部 -->
    <div class="articleHead">
      <p class="round"></p>
      <h2 style="font-family: Georgia, Palatino, serif; font-size: 38px">
        {{ $store.getters.getArticleDetail.articletitle }}
      </h2>
      <p class="articleInfo" style="margin-bottom: 0px; margin-top: 0px">
        <!-- 作者 -->
        <span>
          <el-icon class="el-icon-user"> </el-icon>
          &nbsp;{{ $store.getters.getArticleDetail.createbyName }}
        </span>
        <!-- 时间 -->
        <span>
          <el-icon class="el-icon-time"> </el-icon>&nbsp;{{
            $store.getters.getArticleDetail.createtime
          }}
        </span>
        <!-- 分类 -->
        <span>
          <el-icon class="el-icon-folder-opened"> </el-icon>
          &nbsp;{{ $store.getters.getArticleDetail.category }}
        </span>
        <!-- 浏览量 -->
        <span>
          <el-icon class="el-icon-view"> </el-icon>
          &nbsp;{{ $store.getters.getArticleDetail.review }}
        </span>
      </p>
    </div>
    <!-- 文章内容 -->
    <div class="articleBody">
      <div
        v-highlight
        v-html="compiledMarkdown($store.getters.getArticleDetail.articlecontent)"
        class="markdown-body"
      ></div>
    </div>
    <!-- 文章尾部 -->
    <div class="articleFloot">
      <p>
        版权声明：本站所提供的图文等内容部分源于网络，仅供学习参考，如有侵权，联系删除。转载本站内容请注明相关出处。
      </p>
      <p>
        本文链接：<a :href="thisPath">{{ thisPath }}</a>
      </p>
    </div>
  </div>
</template>

<script>
import { marked } from "marked";
export default {
  isphone:false,
  inject: ["reload"],
  data() {
    return {
      articleDetail: {
        articlecontent: "数据获取失败！请重试！",
      },
      thisPath: "",
    };
  },
  methods: {
    computedWidth() {
      var width = window.document.body.clientWidth;
      if (width < 500) {
        this.isphone = true
      } else {
        this.isphone = false
      }
    },
    /**
     * 格式化日期字符串
     *
     */
    getTimer(stringTime) {
      var minute = 1000 * 60;
      var hour = minute * 60;
      var day = hour * 24;
      var week = day * 7;
      var month = day * 30;
      var time1 = new Date().getTime(); //当前的时间戳
      var time2 = Date.parse(new Date(stringTime)); //指定时间的时间戳
      var time = time1 - time2;

      var result = null;
      if (time < 0) {
        alert("设置的时间不能早于当前时间！");
      } else if (time / month >= 1) {
        result = "" + parseInt(time / month) + "月前";
      } else if (time / week >= 1) {
        result = "" + parseInt(time / week) + "周前";
      } else if (time / day >= 1) {
        result = "" + parseInt(time / day) + "天前";
      } else if (time / hour >= 1) {
        result = "" + parseInt(time / hour) + "小时前";
      } else if (time / minute >= 1) {
        result = "" + parseInt(time / minute) + "分钟前";
      } else {
        result = "刚刚";
      }
      return result;
    },

    async initArticleDetail() {
      
      const id = this.$route.params.id;
      const res = await this.$api.article.getArticleDetailById(id);
      if (res.data.code === 200) {
        this.articleDetail = res.data.data;
        window.sessionStorage.removeItem("articleDetail");
        this.addArticleReview();
        this.$store.dispatch("asyncUpdatedArticleDetail", res.data.data);
      } else {
        this.$message.error("获取文章详情失败！请稍后再试");
      }

      let time = this.articleDetail.createtime; // 当前发布文章的时间戳
      var date = new Date(time); // 初始化日期
      var month = date.getMonth() + 1; // 获取月份
      var day = date.getDate(); // 获取具体日
      this.articleDetail.createtime =
        this.getTimer(time) +
        ("(" +
          month +
          "" + // 三元表达式 日期小于10加上0 例如01
          "." +
          day +
          ")"); // 简化时间 输出格式例如 3天前(7.28)
    },

    async addArticleReview() {
      const id = this.$route.params.id;
      const res = await this.$api.review.addArticleReview({ id: id });
      if (res.data.code === 200) {
        this.getHotArticle();
        return;
      }
      if (res.data.code === 5001) {
        this.$message.error("游览量增加失败！");
      }
    },

    async getHotArticle() {
      const res = await this.$api.article.getHotArticle();
      if (res.data.code === 200) {
        // this.rankList = res.data.data;
        this.$store.dispatch("asyncUpdatedRankList", res.data.data);
      } else {
        this.$message.error("获取热门文章失败！请稍后重试");
      }
    },
  },
  mounted() {
    
    this.initArticleDetail();
    this.thisPath = window.document.location.href;
    this.computedWidth()
  },
  computed: {
    compiledMarkdown() {
      return function (content) {
        return marked.parse(content);
      };
    },
  },
};
</script>
<style scoped>
@import "/src/assets/css/github-markdown.css";
.articleBox {
  height: auto;
  overflow: hidden;
  background-color: #ffffff;
  padding: 0 25px 25px 25px;
  color: #474749;
  border-radius: 10px;
  margin-bottom: 20px;
  margin-top: 70px;
}

/** 3个点 */
.round {
  width: 12px;
  height: 12px;
  background-color: #fc625d;
  border-radius: 16px;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
  margin-bottom: 10px;
}

.articleHead {
  border-bottom: #dbdbdb 1px dashed;
  margin-bottom: 20px;
}

.articleHead h2 {
  line-height: 30px;
}

.articleInfo {
  line-height: 45px;
  color: #888;
  font-size: 13px;
}

.articleInfo span {
  margin-right: 20px;
  display: inline-flex;
  align-items: center;
}

.articleInfo .el-icon {
  margin-right: 8px;
}

/* 尾部样式 */
.articleFloot {
  margin: 20px 0;
  border-left: 5px solid #c353e5;
  padding: 10px 15px;
  background: #f8f8f8;
  font-size: 14px;
  color: #888;
  line-height: 22px;
  box-sizing: border-box;
  overflow: hidden;
  align-items: center;
}

</style>